:root {
  --bg-red: rgb(216, 44, 38);
  --bg-green: rgb(64, 223, 64);
}

body {
  display: flex;
  flex-direction: column;
  font-family:
    Roboto,
    -apple-system,
    BlinkMacSystemFont,
    'Helvetica Neue',
    'Segoe UI',
    'Oxygen',
    'Ubuntu',
    'Cantarell',
    'Open Sans',
    sans-serif;
  color: #86a5b1;
  background-color: #2f3241;
}

* {
  padding: 0;
  margin: 0;
}
input, select, button {
  cursor: pointer;
}
a {
  color: #9feaf9;
  font-weight: 600;
  cursor: pointer;
  text-decoration: none;
  outline: none;
}

.container {
  display: flex;
}

.row {
  flex-basis: 100%;
}


.btn {
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;

}

.btninn {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.5s ease-in-out;

}


#layoutdisplay {
  background-color: rgb(133, 133, 133);
  width: 100%;
  height: 100vh;
  overflow-y: auto;
}

.react-grid-layout {}

.react-tabs__tab-list {
  display: flex;
  justify-content: center;
  border: none;
  border-radius: unset;
}

.react-tabs {}

.react-tabs__tab {
  border: none;
  bottom: unset;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin:0;
}

.react-tabs__tab--selected {
  border: none;
  background: none;
}


.react-tabs__tab:focus:after {
  content: none;
}

.react-tabs__tab-panel {}




.btnd {
  height: inherit;
  color: white;
  margin: 0 2px;
  
}

.tabinn {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

#nosleep{
  height: 100vh;
  position: absolute;
  background-color: #0f0f0fdb;
  width: 100%;
  z-index: 99;
  backdrop-filter: blur(7px);
  display: flex;
  flex-wrap: wrap;
}
#nosleepcontent{
  background-color: #504848db;
  display: flex;
  padding: 10px;
  flex-wrap: wrap;
  height: fit-content;
}
#nosleepconfirm{
  display: flex;
  width: 120px;
  justify-content: space-around;
  margin: auto;
}



